ion-searchbar{
    padding: calc(var(--page-margin) * 1.5) var(--page-margin);
    .searchbar-input-container{
        padding-top: 0px;
      width: 100%;
      margin: auto;
      //flex-basis: 50px;
      .searchbar-input{
        border-radius: 30px;
        border-width: 1px;
        border: 1px;
      }
    }
   }
   .filters-toolbar {
    --padding-start: var(--page-margin);
    --padding-end: var(--page-margin);
    --padding-top: var(--page-margin);
    --padding-bottom: var(--page-margin);
  
    .searchbar-row {
      --ion-grid-column-padding: 0px;
  
      ion-searchbar.items-searchbar {
        padding: 0px;
        // override Ionic fixed height
        height: 100%;
        // Learn more about CSS contain property here: https://termvader.github.io/css-contain/
        contain: content;
      }
  
      .call-to-action-col {
        padding-inline-start: var(--page-margin);
        max-width: fit-content;
        flex-shrink: 0;
        flex-grow: 0;
  
        display: flex;
        justify-content: flex-end;
  
        .filters-btn {
          --padding-start: 0px;
          --padding-end: 0px;
  
          margin: 0px;
          font-size: 18px;
          // override Ionic fixed height
          height: initial;
        }
      }
    }
  
    .range-item-row {
      --ion-grid-column-padding: 0px;
  
      margin-top: var(--page-margin);
      padding-top: var(--page-margin);
      // box-shadow at the top
      box-shadow: inset 0 6px 3px -8px  var(--ion-color-darkest);
  
      .range-header {
        display: flex;
        justify-content: space-between;
        padding-bottom: calc(var(--page-margin) / 2);
  
        .range-value {
          font-size: 12px;
          font-weight: 600;
          letter-spacing: 0.2px;
          color: var(--ion-color-medium);
        }
  
        .range-label {
          font-size: 14px;
          font-weight: 500;
          letter-spacing: 0.2px;
          color: var(--ion-color-medium);
        }
      }
  
      .range-control {
        // override the pin color
        --ion-text-color: var(--ion-color-medium);
  
        padding-top: 0px;
        padding-bottom: 0px;
      }
    }
  }
  
  .date-wrapper {
    align-self: flex-start;
   
    .notification-date {
     
      font-size: 12px;
   
    }
  }
  .lista-list {
    padding: 0px var(--page-margin);
    margin-bottom: calc(var(--page-margin) * 3);
  }
  
  .empty-list-message {
    margin: calc(var(--page-margin) * 3);
    color: rgba(var(--ion-color-dark-rgb), 0.4);
    text-align: center;
  }
  ion-card, .card-ios, .card-md {
    box-shadow: 0 !important;
    text-align: center;
    border:  0.5px solid var(--ion-color-secondary);
    border-top: 2px solid var(--ion-color-secondary);
    display: flex;
   flex-direction: column;
    height: 100%;
    }


    
  ion-item.lista-item {
    --inner-padding-start: 0px;
    --inner-padding-end: 0px;
    --padding-start: 0px;
    --padding-end: 0px;
    --padding-bottom: var(--page-margin);
    --inner-padding-bottom: var(--page-margin);
  
    &:last-child {
      --border-style: none;
      --padding-bottom: 0px;
      --inner-padding-bottom: 0px;
    }
    .date-wrapper {
        align-self: flex-start;
  
        .notification-date {
          margin: 0px;
          font-size: 12px;
          text-align: end;
        }
      }

    .details-scores {
      .score-item {
        display: flex;
        align-items: center;
  
        .score-icon {
          color: var(--ion-color-medium-tint);
          font-size: 24px;
        }
  
        .score-value {
          color: var(--ion-color-medium-tint);
          font-size: 14px;
          margin-left: 4px;
        }
      }
    }

    
    
    .user-details-section {
      --ion-grid-column-padding: 0px;
  
      width: 100%;
      align-items: center;
  
      
      .reviews-wrapper {
        padding: 0px var(--page-margin) var(--page-margin);
    
        .reviews-list {
          margin-bottom: calc(var(--page-margin) / 2);
    
          .review-item {
            &:not(:last-child) {
              padding-bottom: var(--page-margin);
    
              &::after {
                content: '';
                display: block;
                margin: 5px var(--page-margin);
                border-top: 2px solid rgba(var(--ion-color-light-shade-rgb), .4);
              }
            }
    
            
            .review-user {
              --ion-grid-columns: 10;
              --ion-grid-column-padding: 0px;
    
              .user-image-wrapper {
                padding-right: calc(var(--page-margin) / 2);
              }
    
              .user-brief-wrapper {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
    
                padding-right: calc(var(--page-margin) / 2);
    
                .review-user-name {
                  font-size: 16px;
                  margin: 0px;
                  font-weight: 400;
                }
    
                .user-scores {
                  .user-score {
                    .score-title {
                      color: var(--ion-color-medium-tint);
                      font-size: 12px;
                      margin-left: 4px;
                    }
                  }
                }
              }
    
              .review-info-wrapper {
                display: flex;
                flex-direction: column;
                justify-content: space-between;
                align-items: flex-end;
                min-width: 20%;
                min-width: fit-content;
                flex-grow: 0;
    
                .review-date {
                  color: var(--ion-color-medium-tint);
                  font-size: 11px;
                  display: block;
                  width: 100%;
                  text-align: right;
                }
    
                .review-rating {
                  width: 36px;
                  color: var(--ion-color-lightest);
                  border-radius: var(--app-narrow-radius);
                  padding: calc(var(--page-margin) / 4) calc(var(--page-margin) / 2);
                  font-size: 12px;
                  font-weight: 600;
                  display: block;
                  text-align: center;
                  // Default color
                  background-color: var(--ion-color-medium-tint);
    
                  &[ratingBase="1"] {
                    background-color: var(--page-rating-1-color);
                  }
                  &[ratingBase="2"] {
                    background-color: var(--page-rating-2-color);
                  }
                  &[ratingBase="3"] {
                    background-color: var(--page-rating-3-color);
                  }
                  &[ratingBase="4"] {
                    background-color: var(--page-rating-4-color);
                  }
                  &[ratingBase="5"] {
                    background-color: var(--page-rating-5-color);
                  }
                }
              }
            }
    
            .review-message {
              font-size: 14px;
              font-weight: 300;
              line-height: 18px;
              margin: var(--page-margin) 0px;
            }
    
            .review-scores {
              padding: 0px var(--page-margin) var(--page-margin);
              justify-content: space-between;
    
              .review-score {
                justify-content: center;
    
                &:not(:first-child) {
                  padding-left: calc(var(--page-margin) / 2);
                }
    
                &:not(:last-child) {
                  padding-right: calc(var(--page-margin) / 2);
                  border-right: 1px solid var(--ion-color-light-shade);
                }
              }
            }
          }
        }
    
        .details-divider {
          margin: 5px 0px;
        }
    
        .all-reviews-btn {
          margin: 5px 0px;
        }
    
        .write-reviews-btn {
          margin: var(--page-margin) 0px 0px;
        }
      }
    
     
  
      .user-data-wrapper {
        padding-left: var(--page-margin);
        padding-right: calc(var(--page-margin) / 2);
        display: flex;
        flex-direction: column;
        justify-content: center;
  
        .user-info {
          &:not(:last-child) {
            margin-bottom: calc(var(--page-margin) / 2);
          }
  
      

          .user-name {
            margin: 0px;
            margin-bottom: 4px;
            font-size: 16px;
          }
  
          .user-job {
            margin: 0px;
            color: rgba(var(--ion-color-dark-rgb), 0.4);
            font-size: 14px;
          }
        }
      }
  
      .user-actions-wrapper {
        font-size: 14px;
  
        // Make sure we always have space for 10 characters
        // As ch (width of the character '0') unit is not 100% supported, we will use ex (height of the 'x' character) as a fallback
        // See: https://www.quirksmode.org/css/units-values/
        max-width: 10ex; // The 'x' character is semi-square char
        max-width: 10ch; // ch is the only font unit based on the width of characters
  
        .user-action {
          margin: 0px;
        }
      }
    }
  }
  

  .create-form-content {
    --background: var(--page-background);   
  
    .select-user-image-row {
      --ion-grid-column-padding: 0px;
  
      padding: var(--page-margin);
      background-color: var(--page-background-shade);
  
      .user-image-col {
        margin: 0px auto;
        position: relative;
  
        .user-image {
          border-radius: 50%;
          border: solid 3px var(--ion-color-lightest);
          width: 200px;
          height: 200px;
        }
      }
  
      .change-image-btn {
        --border-radius: 50%;
        --padding-start: 0px;
        --padding-end: 0px;
  
        position: absolute;
        right: 0px;
        bottom: var(--page-margin);
        z-index: 3;
        width: 4ch;
        height: 4ch;
        margin: 0px;
  
        .btn-icon {
          font-size: 26px;
        }
      }
    }
  
    .fields-section {
      padding: var(--page-margin) 0px;
      border-bottom: calc(var(--page-margin) * 2) solid var(--page-background-shade);
  
      .section-header {
        padding: 0px var(--page-margin);
        margin-top: 0px;
        margin-bottom: var(--page-margin);
      }
    }
  
    .user-details-fields {
      .inputs-list {
        padding: 0px var(--page-margin);
      }
  
      .input-item {
        --padding-start: 0px;
        --padding-end: 0px;
        --inner-padding-start: 0px;
        --inner-padding-end: 0px;
      }
    }
  
    .user-experience-fields {
      .checkbox-tags {
        padding: 0px calc(var(--page-margin) - var(--page-tags-gutter));
  
      
  
        .checkbox-tag {
          padding: 0px var(--page-tags-gutter);
          margin: var(--page-tags-gutter) 0px;
        }
  
        // Add a deeper selector to override default colors
        &.rounded-checkbox-tags {
          background-color: var(--ion-color-lightest);
  
          .checkbox-tag {
            --border-width: 2px;
            --border-style: solid;
            --border-color: var(--ion-color-medium-shade);
            --checkbox-tag-color: var(--ion-color-medium-shade);
            --checkbox-tag-background: var(--ion-color-lightest);
            --checkbox-tag-active-color: var(--ion-color-lightest);
            --checkbox-tag-active-background: var(--ion-color-darkest);
  
            &.item-checkbox-checked {
              --border-color: var(--ion-color-darkest);
            }
          }
        }
      }
    }
  
    
    .user-languages-fields {
      padding-bottom: 0px;
  
      .range-item-row {
        --ion-grid-column-padding: 0px;
  
        padding-top: var(--page-margin);
        padding-inline-start: var(--page-margin);
        padding-inline-end: var(--page-margin);
        // box-shadow at the top
        box-shadow: inset 0 6px 3px -8px  var(--ion-color-darkest);
  
        .range-header {
          display: flex;
          justify-content: space-between;
          padding-bottom: calc(var(--page-margin) / 2);
  
          .range-value {
            font-size: 12px;
            font-weight: 600;
            letter-spacing: 0.2px;
            color: var(--ion-color-medium-shade);
          }
  
          .range-label {
            font-size: 14px;
            font-weight: 500;
            letter-spacing: 0.2px;
            color: var(--ion-color-medium-shade);
          }
        }
  
        .range-control {
          // override the pin color
          --ion-text-color: var(--ion-color-medium-shade);
  
          padding-top: 0px;
          padding-bottom: 0px;
        }
      }
    }
  }
  
  .form-actions-wrapper {
    --ion-grid-column-padding: calc(var(--page-margin) / 2);
  
    padding: calc(var(--page-margin) / 2);
  
    .submit-btn {
      margin: 0px;
    }
}


.error-container {
  .error-message {
    margin: calc(var(--page-margin) / 2) 0px;
    display: flex;
    align-items: center;
    color: var(--ion-color-danger);
    font-size: 14px;

    ion-icon {
      padding-inline-end: calc(var(--page-margin) / 2);
    }
  }
}
.pac-container{
  z-index: 9999;
}

agm-map {
  height: 300px !important;
  width: 100%;  /* This is really important*/
}

@import "./theme/mixins/inputs/checkbox-tag";
@import "./theme/mixins/inputs/radio-tag";
@import "./theme/mixins/inputs/color-radio";

.filters-content {
  --background: var(--page-background);

	ion-item-divider {
		--background: var(--page-background);
		--padding-bottom: calc(var(--page-margin) / 2);
		--padding-top: calc(var(--page-margin) / 2);
		--padding-start: var(--page-margin);
		--padding-end: var(--page-margin);

		border: none;
	}

  .range-list {
    background-color: var(--ion-color-lightest);

    .range-item {
      --ion-grid-column-padding: 0px;

      padding-top: var(--page-margin);
      padding-inline-start: var(--page-margin);
      padding-inline-end: var(--page-margin);
      // box-shadow at the top
      box-shadow: inset 0 6px 3px -8px  var(--ion-color-darkest);

      .range-header {
        display: flex;
        justify-content: space-between;
        padding-bottom: calc(var(--page-margin) / 2);

        .range-value {
          font-size: 12px;
          font-weight: 600;
          letter-spacing: 0.2px;
          color: var(--ion-color-medium);
        }

        .range-label {
          font-size: 14px;
          font-weight: 500;
          letter-spacing: 0.2px;
          color: var(--ion-color-medium);
        }
      }

      .range-control {
        // override the pin color
        --ion-text-color: var(--ion-color-medium);

        padding-top: 0px;
        padding-bottom: 0px;
      }
    }
  }

  .checkbox-list {
    .checkbox-item {
      --padding-start: var(--page-margin);
      --padding-end: var(--page-margin);
      --inner-padding-end: 0px;

      .checkbox-label {
        font-size: 14px;
				font-weight: 500;
				letter-spacing: 0.2px;
  			color: var(--ion-color-medium);
      }

      ion-checkbox {
        margin-inline-end: 0px;
      }
    }
  }

  .radio-list {
    .radio-group {
      display: block;
    }

    .radio-item {
      --padding-start: var(--page-margin);
      --padding-end: var(--page-margin);
      --inner-padding-end: 0px;

      .radio-label {
        font-size: 14px;
				font-weight: 500;
				letter-spacing: 0.2px;
  			color: var(--ion-color-medium);
      }

      ion-radio {
        margin-inline-start: 0px;
      }
    }
  }

	.checkbox-tags {
		padding: calc(var(--page-margin) / 2) calc(var(--page-margin) - var(--page-tags-gutter));

		@include checkbox-tag();

    .checkbox-tag {
      padding: 0px var(--page-tags-gutter);
      margin: var(--page-tags-gutter) 0px;
    }

    // Add a deeper selector to override default colors
    &.square-checkbox-tags {
      background-color: var(--page-background);

      .checkbox-tag {
        --checkbox-tag-color: var(--ion-color-medium);
        --checkbox-tag-background: var(--ion-color-lightest);
        --checkbox-tag-active-color: var(--ion-color-lightest);
        --checkbox-tag-active-background: var(--ion-color-secondary);
      }
    }

    // Add a deeper selector to override default colors
    &.rounded-checkbox-tags {
  		background-color: var(--ion-color-lightest);

      .checkbox-tag {
        --checkbox-tag-color: var(--ion-color-lightest);
        --checkbox-tag-background: var(--ion-color-light-shade);
        --checkbox-tag-active-color: var(--ion-color-lightest);
        --checkbox-tag-active-background: var(--ion-color-secondary);
      }
    }
	}

  .radio-tags {
    padding: 0px calc(var(--page-margin) - var(--page-tags-gutter));
		background-color: var(--page-background);
    justify-content: space-between;

		@include radio-tag();

    // Add a deeper selector to override default colors
    .radio-tag {
      --radio-tag-color: var(--ion-color-secondary);
      --radio-tag-background: var(--ion-color-lightest);
      --radio-tag-active-color: var(--ion-color-lightest);
      --radio-tag-active-background: var(--ion-color-secondary);

      padding: 0px var(--page-tags-gutter);
      margin: var(--page-tags-gutter) 0px;
    }
	}

  .toggle-list {
    .toggle-item {
      --padding-start: var(--page-margin);
      --padding-end: var(--page-margin);
      --inner-padding-end: 0px;

      .toggle-label {
        font-size: 14px;
				font-weight: 500;
				letter-spacing: 0.2px;
  			color: var(--ion-color-medium);
      }

      ion-toggle {
        padding-inline-end: 0px;
      }
    }
  }

  .counter-list {
    .counter-item {
      --padding-start: var(--page-margin);
      --padding-end: var(--page-margin);
      --inner-padding-end: 0px;

      .counter-label {
        font-size: 14px;
				font-weight: 500;
				letter-spacing: 0.2px;
  			color: var(--ion-color-medium);
      }

      .counter-value {
        text-align: center;
        color: var(--ion-color-secondary);
      }

      app-counter-input {
        --counter-background: transparent;
        --counter-color: var(--ion-color-secondary);
        --counter-border-color: var(--ion-color-secondary);
      }
    }
  }

  .ratings-list {
    .rating-item {
      --padding-start: var(--page-margin);
      --padding-end: var(--page-margin);
      --inner-padding-end: 0px;

      .rating-label {
        font-size: 14px;
				font-weight: 500;
				letter-spacing: 0.2px;
  			color: var(--ion-color-medium);
      }

      app-rating-input {
        --rating-color: var(--ion-color-secondary);
      }
    }
	}

	.color-radio-group {
    // Account for 2% start and end margin set by .color-tag childs
		padding: var(--page-margin) calc(var(--page-margin) - var(--page-color-radio-gutter));
		background-color: var(--ion-color-lightest);

    // Add a deeper selector to override default colors
    .color-radio {
      --color-radio-color: #FFF;
      --color-radio-background: #CCC;
      --color-radio-active-border-rgb: var(--ion-color-darkest-rgb);
      --color-radio-border-radius: 40%;

      flex: 0 0 var(--page-color-radio-width);
      max-width: var(--page-color-radio-width);
      margin: var(--page-color-radio-gutter);

      .color-radio-inner-wrapper {
				display: flex;
				justify-content: center;
				align-items: center;
        width: 100%;
        height: 100%;
			}
    }

		@include color-radio();
	}
}


